import jsonp from 'jsonp'
var commonParams = {
  channel: 'singer',
  page: 'list',
  key: 'all_all_all',
  pagesize: 100,
  pagenum: 1,
  g_tk: 5381,
  loginUin: 0,
  hostUin: 0,
  format: 'jsonp',
  inCharset: 'utf8',
  outCharset: 'utf-8',
  notice: 0,
  platform: 'yqq',
  needNewCode: 0
}
var option = {
  param: 'jsonpCallback',
  prefix: 'fafa'
}
export function getSingerList () {
  let url = 'https://c.y.qq.com/v8/fcg-bin/v8.fcg'
  url += '?' + parse(commonParams)
  return new Promise((resolve, reject) => {
    jsonp(url, option, (err, res) => {
      if (!err) {
        resolve(res)
      } else {
        reject(err)
      }
    })
  })
}
function parse (data) {
  let url = ''
  for (var key in data) {
    let param = data[key] !== undefined ? data[key] : ''
    url += '&' + key + '=' + param
  }
  url = encodeURI(url)
  url = url.substring(1)
  return url
}
export function getSongList (singermid) {
  let url = 'https://c.y.qq.com/v8/fcg-bin/fcg_v8_singer_track_cp.fcg'
  let param = Object.assign(singListParam, {singermid: singermid})
  url += '?' + parse(param)
  return new Promise((resolve, reject) => {
    jsonp(url, option, (err, res) => {
      if (!err) {
        resolve(res)
      } else {
        reject(err)
      }
    })
  })
}
var singListParam = {
  g_tk: 5381,
  loginUin: 0,
  hostUin: 0,
  format: 'jsonp',
  inCharset: 'utf8',
  outCharset: 'utf-8',
  notice: 0,
  platform: 'yqq',
  needNewCode: 0,
  order: 'listen',
  begin: 0,
  num: 100,
  songstatus: 1
}
export  class Singer {
  constructor({id, name}) {
    this.id = id
    this.name = name
    this.avatar = `https://y.gtimg.cn/music/photo_new/T001R300x300M000${id}.jpg?max_age=2592000`
  }
}



