<template>
  <div v-show="playlist.length>0">
    <transition name="full"
                @enter="enter"
                @after-enter="afterEnter"
                @leave="leave"
                @after-leave="afterLeave"
    >
      <div class="full-screen" v-show="fullScreen">
        <div class="background" :style="{backgroundImage:`url(${currentSong.image})`}"></div>
        <div class="top-section">
          <i class="icon-back" @click="back"></i>
          <h2 class="songName">{{currentSong.name}}</h2>
          <h3 class="singer">{{currentSong.singer}}</h3>
        </div>
        <div class="middle-section">
          <div class="circle-wrapper">
            <div class="circle" ref="circle">
              <div class="circle-content">
                <img :src="currentSong.image" class="circle-img" :class="rotate">
              </div>
            </div>
          </div>
        </div>
        <div class="bottom">
          <div></div>
          <div class="progress-wrapper">
            <span class="time time-left">{{format(currentTime)}}</span>
            <div class="progress-bar">
              <progressBar :percent="percent" @percentChange="percentChange" @toHere="toHere"></progressBar>
            </div>
            <span class="time time-right">{{format(currentSong.duration)}}</span>
          </div>
          <div class="icon-wrapper">
            <i :class="iconMode" @click="changeMode"></i>
            <i class="icon-prev" @click="prev" :class="disable"></i>
            <i :class="[playIcon,disable]" @click="togglePlaying"></i>
            <i class="icon-next" @click="next" :class="disable"></i>
            <i class="icon-not-favorite"></i>
          </div>
        </div>
      </div>
    </transition>
    <transition name="mini">
      <div class="bottom-section" @click="openFullScreen" v-show="!fullScreen">
        <div class="circle-wrapper">
          <img :src="currentSong.image" class="circle-img" :class="rotate">
        </div>
        <div class="desc">
          <div class="songName">{{currentSong.name}}</div>
          <div class="singer">{{currentSong.singer}}</div>
        </div>
        <div class="icon-wrapper">
          <progressCircle :radius="32" :percent="percent">
            <i :class="miniIcon" @click.stop="togglePlaying" class="icon-mini"></i>
          </progressCircle>
          <i class="icon-playlist"></i>
        </div>
      </div>
    </transition>
    <audio :src="currentSong.url" ref="audio" @canplay="ready" @error="error" @timeupdate="timeupdate"
           @ended="end"></audio>
  </div>
</template>
<script>
  import {getGeci} from '../common/api/song'
  import shuffle from '../common/api/shuffle'
  import progressCircle from '../base/progress-circle/progress-circle'
  import animations from 'create-keyframe-animation'
  import {mapGetters, mapMutations, mapActions} from 'vuex'
  import {prefixStyle} from '../common/api/dom'
  import progressBar from '../base/progress-bar/progress-bar'
  const transform = prefixStyle('transform')
  export default {
    data() {
      return {
        someReady: false,
        currentTime: 0
      }
    },
    components: {
      progressBar,
      progressCircle
    },
    computed: {
      iconMode() {
        if (this.mode == 0) {
          return 'icon-sequence'
        } else if (this.mode == 1) {
          return 'icon-loop'
        } else {
          return 'icon-random'
        }
      },
      disable() {
        return this.someReady ? '' : 'disable'
      },
      rotate() {
        return this.playing ? 'play' : 'play pause'
      },
      playIcon(){
        return this.playing ? 'icon-pause' : 'icon-play'
      },
      miniIcon() {
        return this.playing ? 'icon-pause-mini' : 'icon-play-mini'
      },
      percent() {
        return this.currentTime / this.currentSong.duration
      },
      ...mapGetters([
        'fullScreen',
        'playlist',
        'currentSong',
        'playing',
        'currentIndex',
        'sequenceList',
        'mode'
      ])
    },
    methods: {
      end() {
        if (this.mode == 1) {
          this.loop()
        } else {
          this.next()
        }
      },
      loop() {
        this.$refs.audio.currentTime = 0
        this.$refs.audio.play()
      },
      back() {
        this.setFullScreen(false)
      },
      openFullScreen() {
        this.setFullScreen(true)
      },
      enter(ele, done) {
        const {x, y, scale} = this._getPosAndScale()
        let animation = {
          0: {
            transform: `translate3d(${x}px,${y}px,0) scale(${scale})`
          },
          60: {
            transform: `translate3d(0,0,0) scale(1.1)`
          },
          100: {
            transform: `translate3d(0,0,0) scale(1)`
          }
        }
        animations.registerAnimation({
          name: 'move',
          animation,
          presets: {
            duration: 400,
            easing: 'linear'
          }
        })
        animations.runAnimation(this.$refs.circle, 'move', done)
      },
      afterEnter(ele, done) {
        animations.unregisterAnimation('move')
        this.$refs.circle.style.animation = ''
      },
      leave(ele, done) {
        this.$refs.circle.style.transition = 'all 0.4s'
        const {x, y, scale} = this._getPosAndScale()
        this.$refs.circle.style[transform] = `translate3d(${x}px,${y}px,0) scale(${scale})`
        this.$refs.circle.addEventListener('transitionend', done)
      },
      afterLeave() {
        this.$refs.circle.style.transition = ''
        this.$refs.circle.style[transform] = ''
      },
      togglePlaying() {
        this.setPlaying(!this.playing)
      },
      toHere(percent) {
        this.$refs.audio.currentTime = this.currentSong.duration * percent
        this.setPlaying(true)
      },
      _getPosAndScale() {
        const targetWidth = 40
        const paddingLeft = 40
        const paddingBottom = 30
        const paddingTop = 80
        const width = innerWidth * 0.8
        const scale = targetWidth / width
        const x = -(innerWidth / 2 - paddingLeft)
        const y = innerHeight - paddingTop - width / 2 - paddingBottom
        return {
          x,
          y,
          scale
        }
      },
      changeMode() {
        let mode = (this.mode + 1) % 3
        this.setPlayMode(mode)
        let list = []
        if (mode == 2) {
          list = shuffle(this.sequenceList)
        } else {
          list = this.sequenceList
        }
        this.resetCurrentIndex(list)
        this.setPlayList(list)
      },
      resetCurrentIndex(list) {
        let index = list.findIndex((item) => {
          return item.id == this.currentSong.id
        })
        this.setCurrentIndex(index)
      },
      prev() {
        if (!this.someReady) {
          return
        }
        let index = this.currentIndex - 1
        if (index === -1) {
          index = this.playlist.length - 1
        }
        this.setCurrentIndex(index)
        this.setPlaying(true)
        this.someReady = false
      },
      next() {
        if (!this.someReady) {
          return
        }
        let index = this.currentIndex + 1
        if (index === this.playlist.length) {
          index = 0
        }
        this.setCurrentIndex(index)
        this.setPlaying(true)
        this.someReady = false
      },
      ready() {
        this.someReady = true
      },
      error(){
        this.someReady = true
      },
      timeupdate(e) {
        this.currentTime = e.target.currentTime
      },
      format(time) {
        time = time | 0
        const minute = time / 60 | 0
        const second = this._pad(time % 60)
        return `${minute}:${second}`
      },
      _pad(num, n = 2) {
        let len = num.toString().length
        while (len < n) {
          num = '0' + num
          len++
        }
        return num
      },
      percentChange(percent) {
        this.$refs.audio.currentTime = this.currentSong.duration * percent
        if (!this.playing) {
          this.togglePlaying()
        }
      },
      ...mapMutations({
        setFullScreen: 'SET_FULL_SCREEN',
        setPlaying: 'SET_PLAYING_STATE',
        setCurrentIndex: 'SET_CURRENT_INDEX',
        setPlayMode: 'SET_PLAY_MODE',
        setPlayList: 'SET_PLAYLIST'
      }),
      ...mapActions([
        'savePlayHistory'
      ])
    },
    watch: {
      currentSong(newSong, oldSong){
        if (newSong.id == oldSong.id) {
          return
        }
        this.$nextTick(() => {
          this.$refs.audio.play()
        })
      },
      playing(value) {
        this.$nextTick(() => {
          if (value) {
            this.$refs.audio.play()
          } else {
            this.$refs.audio.pause()
          }
        })
      },
    },
    mounted() {
    }
  }
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
  @import '../common/stylus/variable';

  .full-screen {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    z-index: 350;
    background-color: #3c3f41;
    .background {
      position: fixed;
      z-index: -1;
      width: 100%;
      height: 100%;
      background-size: cover;
      background-repeat: no-repeat;
      opacity: 0.6;
      filter: blur(20px);
    }
    .top-section {
      position: fixed;
      top: 0;
      width: 100%;
      z-index: 400;
      margin-bottom: 20px;
      .icon-back {
        position: fixed;
        left: 0;
        top: 0;
        padding: 9px;
        font-size: 22px;
        color: #ffcd32;
        transform: rotate(-90deg);
      }
      .songName {
        width: 70%;
        margin: 0 auto;
        line-height: 40px;
        text-align: center;
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
        font-size: 18px;
        color: #fff;
      }
      .singer {
        text-align: center;
        line-height: 20px;
        text-align: center;
        font-size: 14px;
        color: #fff;
      }
    }
    .middle-section {
      position: fixed;
      top: 80px;
      left: 0;
      right: 0;
      bottom: 170px;
      .circle-wrapper {
        position: relative;
        width: 100%;
        padding-top: 80%;
        .circle {
          position: absolute;
          top: 0;
          left: 10%;
          width: 80%;
          height: 100%;
          .circle-content {
            width: 100%;
            height: 100%;
            box-sizing: border-box;
            border: 10px solid rgba(255, 255, 255, 0.1);
            border-radius: 50%;
            .circle-img {
              &.play {
                animation: rotate 20s linear infinite;
              }
              &.pause {
                animation-play-state: paused;
              }
              width: 100%;
              height: 100%;
              border-radius: 50%;
            }
          }
        }
      }
    }
    .bottom {
      position: fixed;
      bottom: 50px;
      width: 100%;
      .progress-wrapper {
        width: 80%;
        margin: 0 auto;
        display: flex;
        margin: 0px auto;
        padding: 10px 0;
        text-align: center;
        .time-left {
          flex: 0 1 30px;
        }
        .time-right {
          flex: 0 1 30px;
        }
        .progress-bar {
          flex: 1 0 auto;
          margin: 0 5px;
        }
      }
      .icon-wrapper {
        width: 80%;
        margin: 0 auto;
        display: flex;
        justify-content: space-around;
        [class^=icon] {
          font-size: 30px;
          color: #ffcd32;
          line-height: 40px;
          &.disable {
            color: $color-text-d;
          }
        }
        .icon-play, .icon-pause {
          font-size: 40px;
        }
      }

    }
  }

  .bottom-section {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    height: 60px;
    z-index: 300;
    background-color: #3c3f41;
    .circle-wrapper {
      float: left;
      display: inline-block;
      width: 40px;
      height: 40px;
      padding: 0 10px 0 20px;
      margin-top: 10px;
      .circle-img {
        &.play {
          animation: rotate 20s linear infinite;
        }
        &.pause {
          animation-play-state: paused;
        }
        width: 100%;
        height: 100%;
        border-radius: 50%;
      }
    }
    .desc {
      float: left;
      line-height: 20px;
      margin-top: 8px;
      .songName {
        margin-bottom: 2px;
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
        font-size: 14px;
        color: #fff;
      }
      .singer {
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
        font-size: 12px;
        color: rgba(255, 255, 255, 0.3);
      }
    }
    .icon-wrapper {
      float: right;
      position: relative;
      [class^=icon] {
        font-size: 32px;
        color: #ffcd32;
        line-height: 60px;
        color: rgba(255, 205, 49, 0.5);
        padding: 10px;
      }
      .icon-mini {
      }
    }
  }

  .full-enter-active, .full-leave-active {
    transition: all 0.4s;
    .top-section, .bottom {
      transition: all 0.4s cubic-bezier(0.550, 0.085, 0.375, 1.650)
    }
  }

  .full-enter, .full-leave-to {
    opacity: 0;
    .top-section {
      transform: translate3d(0, -100px, 0)
    }
    .bottom {
      transform: translate3d(0, 100px, 0)
    }
  }

  @keyframes rotate {
    0% {
      transform: rotate(0deg)
    }
    100% {
      transform: rotate(360deg)
    }
  }

</style>



