<template>
  <div class="singer-detail">
    <h2 class="title">{{title}}</h2>
    <i class="icon-back" @click="goback"></i>
    <div class="bg-img" :style="{backgroundImage:`url(${img})`}" ref="bgImg">
      <div class="random-play" ref="random" v-show="songList.length>0" @click="random">
        <i class="icon-play"></i>
        <span class="play-title">随机播放全部</span>
      </div>
      <div class="overflow"></div>
    </div>
    <div class="flex-overflow" ref="flexOverflow"></div>
    <div class="layout-wrapper" ref="layout">
      <loading v-show="songList.length==0" class="loading"></loading>
      <scroll class="scroll-wrapper" ref="scroll" @scroll="onscroll" :data="['1']">
        <ul class="list-wrapper">
          <li v-for="(item,index) in songList" class="item" @click="select(item,index)">
            <h2 class="song-name">{{item.name}}</h2>
            <p class="song-desc">{{item.singer}}·{{item.album}}</p>
          </li>
        </ul>
      </scroll>
    </div>
  </div>
</template>

<script>
  import {playlistMixin}  from '../../common/api/mixin'
  import {prefixStyle}  from '../../common/api/dom'
  import loading from '../../base/loading/loading'
  const MIN_HEIGHT = 40
  import scroll from '../scroll/scroll.vue'
  import BScroll from 'better-scroll'
  import {mapActions} from 'vuex'
  export default {
    mixins: [playlistMixin],
    data() {
      return {
        scrollY: 0
      }
    },
    props: {
      songList: {
        type: Array,
        default: null
      },
      title: {
        type: String,
        default: ''
      },
      img: {
        type: String,
        default: ''
      }
    },
    methods: {
      handlePlaylist(playlist) {
        const bottom = playlist.length > 0 ? '60px' : ''
        this.$refs.scroll.$el.style.bottom = bottom
        this.$refs.scroll.refresh()
      },
      random() {
        this.randomPlay({
          list: this.songList
        })
      },
      goback(){
        this.$router.go(-1)
      },
      onscroll(pos){
        this.scrollY = pos.y
      },
      select(item, index) {
        this.selectPlay({
          list: this.songList,
          index: index
        })
      },
      ...mapActions([
        'selectPlay',
        'randomPlay'
      ])
    },
    mounted() {
      this.$refs.layout.style.top = `${this.$refs.bgImg.offsetHeight}px`
      this.$refs.flexOverflow.style.bottom = `${innerHeight - this.$refs.bgImg.offsetHeight}px`
      this.imgHeight = this.$refs.bgImg.offsetHeight
    },
    components: {
      scroll,
      loading
    },
    watch: {
      scrollY(value) {
        if (-value >= this.imgHeight - MIN_HEIGHT) {
          this.$refs.flexOverflow.style.height = `${this.imgHeight - MIN_HEIGHT}px`
          this.$refs.bgImg.style.zIndex = 150
          this.$refs.bgImg.style.height = `${MIN_HEIGHT}px`
          this.$refs.bgImg.style.paddingTop = `0`
          this.$refs.random.style.display = `none`
        } else if (value < 0) {
          this.$refs.flexOverflow.style.height = `${-value}px`
          this.$refs.bgImg.style.paddingTop = `70%`
          this.$refs.bgImg.style.height = `0`
          this.$refs.bgImg.style.zIndex = 0
          this.$refs.random.style.display = `block`
        } else if (value > 0) {
          this.$refs.flexOverflow.style.height = `0px`
          let percent = value / this.$refs.bgImg.offsetHeight
          let scale = 1 + percent
          this.$refs.bgImg.style[prefixStyle('transform')] = `scale(${scale})`
          this.$refs.bgImg.style.zIndex = `150`
        }
      }
    }
  }
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
  .singer-detail {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 120;
    .bg-img {
      position: relative;
      background-size: cover;
      padding-top: 70%;
      width: 100%;
      background-color: #222222;
      .random-play {
        left: 50%;
        transform: translateX(-50%);
        position: absolute;
        bottom: 20px;
        border: 1px solid #ffcd32;
        color: #ffcd32;
        border-radius: 100px;
        font-size: 0;
        padding: 7px 0;
        width: 135px;
        box-sizing: border-box;
        text-align: center;
        z-index: 110;
        .icon-play {
          margin-right: 6px;
          font-size: 16px;
          color: #ffcd32;
          vertical-align: middle;
        }
        .play-title {
          font-size: 12px;
          vertical-align: middle;
        }
      }
      .overflow {
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        background-color: rgba(7, 17, 27, 0.4);
        z-index: 105;
      }
    }
    .title {
      position: fixed;
      top: 0;
      width: 100%;
      text-align: center;
      line-height: 40px;
      font-size: 18px;
      z-index: 220;
    }
    .icon-back {
      position: fixed;
      left: 0;
      top: 0;
      padding: 10px;
      font-size: 22px;
      color: #ffcd32;
      z-index: 220;
    }
    .layout-wrapper {
      .scroll-wrapper {
        width: 100%;
        height: 100%;
        .list-wrapper {
          padding: 20px 30px;
          font-size: 14px;
          .item {
            height: 64px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            .song-name {
              overflow: hidden;
              text-overflow: ellipsis;
              white-space: nowrap;
            }
            .song-desc {
              text-overflow: ellipsis;
              overflow: hidden;
              white-space: nowrap;
              margin-top: 10px;
              color: rgba(255, 255, 255, 0.3);
            }
          }

        }
      }
      .loading {
        position: absolute;
        top: 45%;
      }
      position: fixed;
      bottom: 0;
      left: 0;
      right: 0;
      z-index: 120;
      background: #222;
    }
    .flex-overflow {
      width: 100%;
      height: 0px;
      position: fixed;
      background: #222;
      z-index: 120;
    }
  }

</style>



