<template>
    <div class="loading">
      <img src="./loading.gif" width="24" height="24">
      <p class="title">{{title}}</p>
    </div>
</template>

<script>
    export default {
        props:{
            title:{
                type:String,
                default:'正在载入...'
            }
        }
    }
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
  @import '../../common/stylus/variable';
  .loading{
    width: 100%;
    text-align: center;
    .title{
      line-height: 20px;
      font-size: $font-size-small;
      color: $color-text-l;
    }
  }

</style>



