<template>
  <div ref="carousel">
    <slot></slot>
    <div class="dot-wrapper" v-if="imgLength>0">
      <span v-for="(item,index) in imgWrapper.children" class="dot" :class="{active:index==currentIndex}">
      </span>
    </div>
  </div>
</template>

<script>
  import $ from 'jquery'
  export default {
    data() {
      return {
        imgWrapper: null,
        currentIndex: 1,
        oneImgWidth: 0,
        imgLength: 0,
        timer: null,
        isAutoPlay: this.carouselParams.isAutoPlay,
        interval: this.carouselParams.interval,
        slideTime: this.carouselParams.slideTime
      }
    },
    methods: {
      initWidth() {
        if (this.$refs.carousel) {
          var width = 0
          Array.from(this.$refs.carousel.children[0].children[0].children).map((item) => {
            item.style.width = innerWidth + 'px'
            width += item.offsetWidth
            this.oneImgWidth = item.offsetWidth
          })
          this.imgWrapper = this.$refs.carousel.children[0].children[0]
          this.imgWrapper.style.width = width + 'px'
        }
      },
      onResize() {
        window.onresize = () => {
          this.initWidth()
          this.initCarousel()
        }
      },
      onSlide () {
        var startX
        var offsetX
        this.$refs.carousel.addEventListener('touchstart', (event) => {
          startX = event.touches[0].clientX
        })
        this.$refs.carousel.addEventListener('touchmove', (event) => {
          var moveX = event.touches[0].clientX
          offsetX = moveX - startX
          if (offsetX > 0) {
            this.slideRight(offsetX, false)
          } else {
            this.slideLeft(offsetX, false)
          }
        })
        this.$refs.carousel.addEventListener('touchend', (event) => {
          if (Math.abs(offsetX) >= this.oneImgWidth / 2) {
            if (offsetX > 0) {
              this.slideRight(offsetX, true)
            } else {
              this.slideLeft(offsetX, true)
            }
          } else {
            $(this.imgWrapper).animate({'marginLeft': -this.currentIndex * this.oneImgWidth + 'px'}, this.slideTime)
          }
        })
      },
      slideLeft(offsetX, isOver) {
        if (isOver) {
          this.currentIndex--
          $(this.imgWrapper).animate({'marginLeft': -this.currentIndex * this.oneImgWidth + 'px'}, this.slideTime)
          if (this.currentIndex === 0) {
            this.currentIndex = this.imgLength - 2
            $(this.imgWrapper).css({'marginLeft': -this.currentIndex * this.oneImgWidth + 'px'}, this.slideTime)
          }
        } else {
          $(this.imgWrapper).css({'marginLeft': -this.currentIndex * this.oneImgWidth - offsetX + 'px'})
        }
      },
      slideRight(offsetX, isOver) {
        if (isOver) {
          this.currentIndex++
          $(this.imgWrapper).animate({'marginLeft': -this.currentIndex * this.oneImgWidth + 'px'}, this.slideTime)
          if (this.currentIndex === this.imgLength - 1) {
            this.currentIndex = 1
            $(this.imgWrapper).css({'marginLeft': -this.currentIndex * this.oneImgWidth + 'px'})
            $(this.imgWrapper).animate({'marginLeft': -this.currentIndex * this.oneImgWidth + 'px'}, 0)
          }
        } else {
          $(this.imgWrapper).css({'marginLeft': -this.currentIndex * this.oneImgWidth - offsetX + 'px'})
        }
      },
      clone() {
        let firstChild = $(this.imgWrapper.firstChild).clone(true)
        let lastChild = $(this.imgWrapper.lastChild).clone(true)
        firstChild.appendTo(this.imgWrapper)
        lastChild.prependTo(this.imgWrapper)
        this.imgLength = this.$refs.carousel.children[0].children[0].children.length
      },
      initCarousel() {
        $(this.imgWrapper).css('marginLeft', -this.oneImgWidth * this.currentIndex + 'px')
      },
      rideCarousel() {
        var self=this
        function timeOut() {
          self.timer=setTimeout(() => {
            self.slideRight(null, true)
            timeOut()
          }, self.interval)
        }
        timeOut()
      }
    },
    destroyed() {
      clearTimeout(this.timer)
    },
    mounted () {
      this.initWidth()
      this.initCarousel()
      this.clone()
      this.onResize()
      this.onSlide()
      if (this.isAutoPlay) {
        this.rideCarousel()
      }
    },
    props: {
      carouselParams: {
        type: Object
      }
    }
  }
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
  .dot-wrapper {
    position: absolute;
    bottom: 6px;
    left: 0;
    right: 0;
    padding: 0 6px;
    text-align: center;
    z-index: 5;
    .dot {
      display: inline-block;
      margin: 0 4px;
      width: 8px;
      height: 8px;
      border-radius: 50%;
      background: rgba(255, 255, 255, 0.5);
      &.active {
        background-color: white;
      }
      &:first-child, &:last-child {
        display: none;
      }
    }
  }
</style>



