<template>
  <transition name="slide">
    <music-list :title="title" :img="bgImage" :songList="songs"></music-list>
  </transition>
</template>

<script>
  import musicList from '../../base/music-list/music-list'
  import { getDisc } from '../../common/api/recommmend'
  import { mapGetters } from 'vuex'
  const ERR_OK = 0
  export default {
    computed: {
      title() {
        return this.disc.dissname
      },
      bgImage() {
        return this.disc.imgurl
      },
      ...mapGetters([
        'disc'
      ])
    },
    data() {
      return {
        songs: []
      }
    },
    created() {
      this._getSongList()
    },
    methods: {
      _getSongList() {
        if (!this.disc.dissid) {
          this.$router.push('/recommend')
          return
        }
        getDisc(this.disc.dissid).then((res) => {
          if (res.code === ERR_OK) {
            console.log(res.cdlist[0].songlist)
            this.songs = this._normalizeSongs(res.cdlist[0].songlist)
          }
        })
      },
      _normalizeSongs(list) {
        let ret = []
        list.forEach((musicData) => {
          if (musicData.songid && musicData.albummid) {
            ret.push(this.createSong(musicData))
          }
        })
        return ret
      },
      createSong(item) {
        let returnItem={
          id: item.songid,
          mid: item.albummid,
          singer: item.singer[0].name,
          name: item.songname,
          album: item.albumname,
          duration: item.interval,
          image: 'https://y.gtimg.cn/music/photo_new/T002R300x300M000' + item.albummid + '.jpg?max_age=2592000',
          url: 'http://ws.stream.qqmusic.qq.com/' + item.songid + '.m4a?fromtag=46'
        }
        return returnItem
      }
    },
    components: {
      musicList
    }
  }
</script>

<style scoped lang="scss" rel="stylesheet/scss">
  .slide-enter-active, .slide-leave-active {
    transition: all 0.3s
  }

  .slide-enter, .slide-leave-to {
    transform: translate3d(100%, 0, 0)
  }

</style>



