import jsonp from 'jsonp'
import { option, commonParams, songListParams } from './config'
export function getRecommend () {
  let url = 'https://c.y.qq.com/musichall/fcgi-bin/fcg_yqqhomepagerecommend.fcg'
  url += '?' + parse(commonParams)
  return new Promise((resolve, reject) => {
    jsonp(url, option, (err, res) => {
      if (!err) {
        resolve(res)
      } else {
        reject(err)
      }
    })
  })
}
function parse (data) {
  let url = ''
  for (var key in data) {
    let param = data[key] !== undefined ? data[key] : ''
    url += '&' + key + '=' + param
  }
  url = encodeURI(url)
  url = url.substring(1)
  return url
}

export function getSongList () {
  let url = 'https://c.y.qq.com/v8/fcg-bin/fcg_first_yqq.fcg'
  url += '?' + parse(commonParams) + '&' + parse(songListParams)
  return new Promise((resolve, reject) => {
    jsonp(url, option, (err, res) => {
      if (!err) {
        resolve(res)
      } else {
        reject(err)
      }
    })
  })
}



export function getDisc (disstid) {
  let url = 'https://c.y.qq.com/qzone/fcg-bin/fcg_ucc_getcdinfo_byids_cp.fcg'

  const data = Object.assign({}, commonParams, {
    disstid,
    type: 1,
    json: 1,
    utf8: 1,
    onlysong: 0,
    platform: 'yqq',
    hostUin: 0,
    needNewCode: 0
  })

  url += '?' + parse(data)
  return new Promise((resolve, reject) => {
    jsonp(url, option, (err, res) => {
      if (!err) {
        resolve(res)
      } else {
        reject(err)
      }
    })
  })
}



