<template>
  <div class="singer">
    <list-view :data=allSingerList @selectSinger="selectSinger"></list-view>
    <router-view></router-view>
  </div>
</template>

<script>
  import {createImageUrl} from '../common/api/createImageUrl'
  import {getSingerList} from '../common/api/singer'
  import listView from '../base/listview/listview.vue'
  import {mapMutations} from 'vuex'
  export default {
    data(){
      return {
        allSingerList: [],
        hotSingers: []
      }
    },
    methods: {
      _getSingList() {
        getSingerList().then((res) => {
          this.singerList = res.data.list
          this.fixSingerList(this.singerList)
        })
      },
      fixSingerList(data) {
        var hotSingers = {
          title: '热门',
          array: []
        }
        var allSingers = {}
        var allSingerList = []
        var reg = /[A-Za-z]/
        data.map((item, index, array) => {
          if (reg.test(item.Findex)) {
            if (index <= 9) {
              hotSingers.array.push(item)
            }
            if (!allSingers[item.Findex]) {
              allSingers[item.Findex] = {
                title: item.Findex,
                array: [item]
              }
            } else {
              allSingers[item.Findex].array.push(item)
            }
          }
        })
        for (let key in allSingers) {
          allSingerList.push(allSingers[key])
        }
        allSingerList.sort(function (a, b) {
          return a.title.charCodeAt(0) - b.title.charCodeAt(0)
        })
        allSingerList.unshift(hotSingers);
        this.allSingerList = allSingerList
        this.hotSingers = hotSingers
        console.log(this.allSingerList)
      },
      selectSinger(item) {
        this.$router.push({path: '/singer/'+item.Fsinger_mid})
        this.setSinger(item)
      },
      ...mapMutations({
        setSinger:'SET_SINGER'
      })
    },
    created(){
      this._getSingList()
    },
    components: {
      listView
    }
  }
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
  .singer {
    position: fixed;
    top: 88px;
    bottom: 0;
    width: 100%;
  }

</style>



