import { Base64 } from 'js-base64'
import { getLyric } from './song'
export class Song {
  constructor ({id, mid, singer, name, album, duration, image, url}) {
    this.id = id
    this.mid = mid
    this.singer = singer
    this.name = name
    this.album = album
    this.duration = duration
    this.image = image
    this.url = url
  }

  getLyric () {
    if (this.lyric) {
      return Promise.resolve(this.lyric)
    }
    return new Promise((resolve, reject) => {
      getLyric(this.mid).then((res) => {
        console.log(res)
        if (res.retcode === 0) {
          this.lyric = Base64.decode(res.lyric)
          resolve(this.lyric)
        } else {
          reject('no lyric')
        }
      })
    })
  }
}

export function recreateSong (song) {
  return new Song({
    id: song.musicData.songid,
    mid: song.musicData.albummid,
    singer: song.musicData.singer[0].name,
    name: song.musicData.songname,
    album: song.musicData.albumname,
    duration: song.musicData.interval,
    image: 'https://y.gtimg.cn/music/photo_new/T002R300x300M000' + song.musicData.albummid + '.jpg?max_age=2592000',
    url: 'http://ws.stream.qqmusic.qq.com/' + song.musicData.songid + '.m4a?fromtag=46'
  })
}



