<template>
  <transition name="slide">
    <musicList :songList="matureList" :title="title" :img="img"></musicList>
  </transition>
</template>

<script>
  import {createBigImageUrl} from '../common/api/createImageUrl'
  import musicList from '../base/music-list/music-list.vue'
  import {mapGetters} from 'vuex'
  import {getSongList} from '../common/api/singer'
  import {ERR_OK} from '../common/api/config'
  import {recreateSong} from '../common/api/handleSingerDetail'
  export default {
    data() {
      return {
        title: '',
        img: '',
        matureList: [],
      }
    },
    computed: {
      ...mapGetters([
        'singer'
      ])
    },
    mounted(){
      this._getSongList()
    },
    methods: {
      _getSongList() {
        if (!this.singer.Fsinger_mid) {
          this.$router.push('/singer')
          return
        }
        getSongList(this.singer.Fsinger_mid).then((res) => {
          if (res.code == ERR_OK) {
            this.title = res.data.singer_name
            this.img = createBigImageUrl(res.data.singer_mid)
            let matureList = []
            res.data.list.forEach((item) => {
              matureList.push(recreateSong(item))
            })
            this.matureList = matureList
          }
        })
      },
    },
    components: {
      musicList
    }
  }
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
  .slide-enter-active, .slide-leave-active {
    transition: all 0.3s
  }
  .slide-enter, .slide-leave-to {
    transform: translate3d(100%, 0, 0)
  }
</style>



