<template>
  <div class="recommend">
    <scroll :data="hotList" ref="scroll" class="recommend-content">
      <div>
        <carousel :carouselParams=carouselParams v-if="slider.length>0" class="carousel-wrapper">
          <div class="view-port">
            <div class="img-wrapper">
              <a :href=item.linkUrl v-for="item in slider" class="link">
                <img :src=item.picUrl class="img" @load="loadImage">
              </a>
            </div>
          </div>
        </carousel>
        <div>
          <h1 class="list-title">热门歌单推荐</h1>
          <loading v-if="!hotList.length" class="loading-wrapper"></loading>
          <ul class="list-wrapper">
            <li v-for="(item,index) in hotList" class="list-item" @click="click(item)">
              <div>
                <img v-lazy="item.imgurl" width="60" height="60">
              </div>
              <div class="message">
                <div class="author">
                  {{item.author}}
                </div>
                <div class="dissname">
                  {{item.dissname}}
                </div>
              </div>
            </li>
          </ul>
        </div>
      </div>
    </scroll>
    <router-view class="view"></router-view>
  </div>
</template>

<script>
  import loading from 'src/base/loading/loading'
  import scroll from 'src/base/scroll/scroll'
  import carousel from '../base/carousel/carousel'
  import { getRecommend, getSongList } from '../common/api/recommmend'
  import { ERR_OK } from '../common/api/config'
  import {mapMutations} from 'vuex'
  export default {
    data() {
      return {
        slider: [],
        hotList: [],
        carouselParams: {
          isAutoPlay: true,
          interval: 5000,
          slideTime: 400
        }
      }
    },
    created () {
      this._getRecommend()
      setTimeout(() => {
        this._getSongList()
      }, 0)
    },
    methods: {
      _getRecommend() {
        getRecommend().then((res) => {
          if (res.code === ERR_OK) {
            this.slider = res.data.slider
          }
        })
      },
      _getSongList() {
        getSongList().then((res) => {
          if (res.code === ERR_OK) {
            this.hotList = res.data.hotdiss.list
            console.log(this.hotList)
          }
        })
      },
      loadImage() {
        if (!this.checked) {
          this.checked = true
          this.$refs.scroll.refresh()
        }
      },
      click(item) {
        this.$router.push({
          path: `/recommend/${item.dissid}`
        })
        this.setDisc(item)
      },
      ...mapMutations({
        setDisc:'SET_DISC'
      })
    },
    components: {
      carousel,
      scroll,
      loading
    }
  }

</script>

<style rel="stylesheet/scss" lang="scss" scoped>
  .carousel-wrapper {
    touch-action: none;
    position: relative;
    .view-port {
      overflow: hidden;
      white-space: nowrap;
      .img-wrapper {
        .link {
          display: inline-block;
          .img {
            height: 100%;
            width: 100%;
            vertical-align: bottom;
          }
        }
      }
    }
  }

  .list-title {
    height: 65px;
    line-height: 65px;
    text-align: center;
    font-size: 14px;
    color: #ffcd32;
  }

  .loading-wrapper {
    position: absolute;
    top: 50%;
  }

  .list-wrapper {
    .list-item {
      display: flex;
      box-sizing: border-box;
      align-items: center;
      padding: 0 20px 20px 20px;
      .message {
        flex: 1;
        margin-left: 20px;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        font-size: 14px;
        .author {
          margin-bottom: 20px;
        }
        .dissname {
          color: rgba(255, 255, 255, 0.3);;
        }
      }
    }
  }

  .recommend {
    position: fixed;
    width: 100%;
    top: 88px;
    bottom: 0;
    z-index: 5;
    .recommend-content {
      width: 100%;
      height: 100%;
      overflow: hidden;
    }
  }
</style>



