<template>
  <div class="wrapper">
    <scroll class="list-view" :data="data" ref="leftScroll" @scroll="scroll">
      <ul class="list-group">
        <li v-for="group in data" ref="groups">
          <h2 class="list-title" ref="titles">{{group.title}}</h2>
          <ul>
            <li v-for="(item,index) in group.array" class="list-item" @click="selectSinger(item)">
              <img :src=createImageUrl(item.Fsinger_mid) class="img">
              <span class="name">{{item.Fsinger_name}}</span>
            </li>
          </ul>
        </li>
      </ul>
    </scroll>
    <div class="list-shortcut">
      <ul>
        <li v-for="(item,index) in data" class="shortcut-item" @touchstart="ontouchstart($event,index)"
            @touchmove.stop.prevent="ontouchmove" :class="{currentIndex:currentIndex==index}">
          {{item.title.substr(0, 1)}}
        </li>
      </ul>
    </div>
    <div class="flex-title" ref="flextitle" v-if="pos.y<0">
      {{data[currentIndex].title}}
    </div>
  </div>
</template>

<script>
  const TITLE_HEIGHT = 30
  import scroll from '../scroll/scroll'
  import BScroll from 'better-scroll'
  export default {
    data() {
      return {
        heightList: [],
        currentIndex: 0,
        pos: {y: 1}
      }
    },
    props: {
      data: {
        type: Array,
        default: null,
      }
    },
    components: {
      scroll
    },
    methods: {
      createImageUrl(mid) {
        return 'https://y.gtimg.cn/music/photo_new/T001R150x150M000' + mid + '.jpg?max_age=2592000'
      },
      ontouchstart(event, index) {
        this.$refs.leftScroll.scrollToElement(this.$refs.groups[index])
        this.touch = {}
        this.touch.index = index
        this.touch.y1 = event.touches[0].clientY
        this.currentIndex = index
      },
      ontouchmove(event) {
        this.touch.y2 = event.touches[0].clientY
        let offset = Math.floor((this.touch.y2 - this.touch.y1) / 18)
        let index = this.touch.index + offset
        if (index > 22 || index < 0) {
          return
        }
        this.currentIndex = index
        this.$refs.leftScroll.scrollToElement(this.$refs.groups[index])
      },
      calculateHeight() {
        let heightList = []
        let height = 0
        heightList.push(height)
        this.$refs.groups.forEach((item, index, array) => {
          let currentHeight = item.clientHeight
          height += currentHeight
          heightList.push(height)
        })
        this.heightList = heightList
        console.log(this.heightList)
      },
      scroll(pos) {
        this.pos = pos
        this.heightList.forEach((item, index, array) => {
          let currentHeight = array[index]
          let nextHeight = array[index + 1]
          if (currentHeight < -pos.y && -pos.y < nextHeight) {
            this.currentIndex = index
          }
        })
        this.toLastDistance(pos)
      },
      toLastDistance(pos) {
        if (this.currentIndex < this.$refs.groups.length - 1) {
          let differ = this.$refs.groups[this.currentIndex + 1].offsetTop - (-pos.y) - TITLE_HEIGHT
          if (differ <= 0) {
            this.$refs.flextitle.style.transform = 'translateY(' + (differ) + 'px)'
          }
        }
      },
      selectSinger(item) {
          this.$emit('selectSinger',item)
      }
    },
    watch: {
      data(){
        this.$nextTick(() => {
          this.calculateHeight()
        })
      },
      currentIndex() {
        if (this.$refs.flextitle) {
          this.$refs.flextitle.style.transform = 'translateY(0px)'
        }
      }
    }
  }
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
  @import '../../common/stylus/base';

  .wrapper {
    position: relative;
    width: 100%;
    height: 100%;
    overflow: hidden;
    background: $color-background;
    .list-view {
      position: relative;
      width: 100%;
      height: 100%;
      overflow: hidden;
      background: $color-background;
      .list-group {
        .list-title {
          height: 30px;
          line-height: 30px;
          padding-left: 20px;
          font-size: 12px;
          color: rgba(255, 255, 255, 0.5);
          background: #333;
        }
        .list-item {
          display: flex;
          align-items: center;
          padding: 20px 0 0 30px;
          .img {
            width: 50px;
            height: 50px;
            border-radius: 50%;
          }
          .name {
            margin-left: 20px;
            color: rgba(255, 255, 255, 0.5);
            font-size: 14px;
          }
        }

      }
    }

    .list-shortcut {
      position: absolute;
      width: 20px;
      top: 50%;
      right: 0;
      transform: translateY(-50%);
      padding: 20px 0;
      border-radius: 10px;
      text-align: center;
      background: rgba(0, 0, 0, 0.3);
      font-family: Helvetica;
      .shortcut-item {
        padding: 3px;
        line-height: 1;
        color: rgba(255, 255, 255, 0.5);
        font-size: 12px;
      }
      .currentIndex {
        color: #ffcd32;
      }
    }
    .flex-title {
      position: absolute;
      top: 0;
      height: 30px;
      line-height: 30px;
      padding-left: 20px;
      font-size: 12px;
      width: 100%;
      color: rgba(255, 255, 255, 0.5);
      background: #333;
    }
  }

</style>



