import jsonp from 'jsonp'
let commonParams = {
  g_tk: 1928093487,
  inCharset: 'utf-8',
  outCharset: 'utf-8',
  notice: 0,
  format: 'jsonp'
}
let option = {
  param: 'jsonpCallback',
  prefix: 'fa'
}

function parse (data) {
  let url = ''
  for (var key in data) {
    let param = data[key] !== undefined ? data[key] : ''
    url += '&' + key + '=' + param
  }
  url = encodeURI(url)
  url = url.substring(1)
  return url
}

export const ERR_OK = 0

export function getTopList () {
  let url = 'https://c.y.qq.com/v8/fcg-bin/fcg_myqq_toplist.fcg'

  const data = Object.assign({}, commonParams, {
    uin: 0,
    needNewCode: 1,
    platform: 'h5'
  })

  url += '?' + parse(data)
  return new Promise((resolve, reject) => {
    jsonp(url, option, (err, res) => {
      if (!err) {
        resolve(res)
      } else {
        reject(err)
      }
    })
  })
}
