<template>
  <div class="progress-bar" ref="progressBar" @click="toHere">
    <div class="bar-inner">
      <div class="progress" ref="progress"></div>
      <div class="progress-btn" ref="progressBtn"
           @touchstart="progressTouchStart"
           @touchend="progressTouchEnd"
           @touchmove="progressTouchMove"
      ></div>
    </div>
  </div>
</template>

<script>
  import {prefixStyle} from '../../common/api/dom'
  const transform = prefixStyle('transform')
  const btnWidth = 16
  export default {
    props: {
      percent: {
        type: Number,
        default: 0
      }
    },
    created(){
      this.touch = {}
    },
    methods: {
      progressTouchStart(e) {
        this.touch.initiated = true
        this.touch.statX = e.touches[0].clientX
        this.touch.left = this.$refs.progress.clientWidth
      },
      progressTouchEnd() {
        this.touch.initiated = false
        this._triggerPercent()
      },
      _triggerPercent() {
        let barWidth = this.$refs.progressBar.clientWidth - btnWidth
        const percent = this.$refs.progress.clientWidth / barWidth
        this.$emit('percentChange', percent)
      },
      progressTouchMove(e) {
        if (!this.touch.initiated) {
          return
        }
        const deltaX = e.touches[0].pageX - this.touch.statX
        const offsetWidth = Math.min(this.$refs.progressBar.clientWidth - btnWidth, Math.max(0, this.touch.left + deltaX))
        this._offset(offsetWidth)
      },
      _offset(offsetWidth) {
        this.$refs.progress.style.width = `${offsetWidth}px`
        this.$refs.progressBtn.style[transform] = `translate3d(${offsetWidth}px,0,0)`
      },
      toHere(e) {
        let left=this.$refs.progressBar.getBoundingClientRect().left
        let offsetWidth=e.pageX-left
//        let offset = e.offsetX
        let percent = offsetWidth / this.$refs.progressBar.clientWidth
        this.$emit('toHere', percent)
      }
    },
    watch: {
      percent(value) {
        if (value >= 0 && !this.touch.initiated) {
          let barWidth = this.$refs.progressBar.clientWidth - btnWidth
          let offsetWidth = value * barWidth
          this._offset(offsetWidth)
        }
      }
    }
  }
</script>

<style scoped lang="scss" rel="stylesheet/scss">
  .bar-inner {
    width: 100%;
    background: rgba(0, 0, 0, 0.3);
    height: 4px;
    position: relative;
    top: 5px;
    .progress {
      background: #ffcd32;
      height: 4px;
    }
    .progress-btn {
      position: relative;
      box-sizing: border-box;
      top: -10px;
      width: 16px;
      height: 16px;
      border: 3px solid #fff;
      border-radius: 50%;
      background: #ffcd32;
    }
  }
</style>



