<template>
  <transition name="slide">
    <music-list :rank="rank" :title="title" :img="bgImage" :songList="songs"></music-list>
  </transition>
</template>

<script type="text/ecmascript-6">
  import MusicList from '../../base/music-list/music-list'
  import {getMusicList} from '../../common/api/rank'
  import {ERR_OK} from '../../common/api/rank'
  import {mapGetters} from 'vuex'

  export default {
    computed: {
      title() {
        console.log(this.topList)
        return this.topList.topTitle
      },
      bgImage() {
        if (this.songs.length) {
          return this.songs[0].image
        }
        return ''
      },
      ...mapGetters([
        'topList'
      ])
    },
    data() {
      return {
        songs: [],
        rank: true
      }
    },
    created() {
      this._getMusicList()
    },
    methods: {
      _getMusicList() {
        if (!this.topList.id) {
          this.$router.push('/rank')
          return
        }
        getMusicList(this.topList.id).then((res) => {
          if (res.code === ERR_OK) {
            console.log(res)
            this.songs = this._normalizeSongs(res.songlist)
          }
        })
      },
      _normalizeSongs(list) {
        let ret = []
        list.forEach((item) => {
          const musicData = item.data
          if (musicData.songid && musicData.albummid) {
            ret.push(this.createSong(musicData))
          }
        })
        return ret
      },
      createSong(item) {
        let returnItem={
          id: item.songid,
          mid: item.albummid,
          singer: item.singer[0].name,
          name: item.songname,
          album: item.albumname,
          duration: item.interval,
          image: 'https://y.gtimg.cn/music/photo_new/T002R300x300M000' + item.albummid + '.jpg?max_age=2592000',
          url: 'http://ws.stream.qqmusic.qq.com/' + item.songid + '.m4a?fromtag=46'
        }
        return returnItem
      }
    },
    components: {
      MusicList
    }
  }
</script>

<style scoped lang="scss" rel="stylesheet/scss">
  .slide-enter-active, .slide-leave-active{
    transition: all 0.3s ease
  }
  .slide-enter, .slide-leave-to{
    transform: translate3d(100%, 0, 0)
  }

</style>



